import "./Input.css";

const Input = ({
  type,
  children,
  name,
  onChange,
  value,
  disabled,
  className,
  icon,
  placeholder,
  onClick,
  cols,
  rows,
  accept,
  options,
  id,
  autoComplete
}) => {
  switch (type) {
    case "text":
      return (
        <div className="input">
          {icon}
          <input
            type={type}
            required
            value={value}
            name={name}
            placeholder={placeholder}
            className={className}
            onChange={onChange}
            autoComplete={autoComplete}
          />
        </div>
      );
    case "number":
      return (
        <div className="input">
          {icon}
          <input
            type={type}
            required
            value={value}
            name={name}
            placeholder={placeholder}
            className={className}
            onChange={onChange}
            autoComplete={autoComplete}
          />
        </div>
      );
    case "select":
      return (
        <div className="input">
          {icon}
          <select
            required
            name={name}
            onChange={onChange}
            className={className}
            type={type}
            value={value}
            placeholder={placeholder}
            autoComplete={autoComplete}
          >
            {children}
          </select>
        </div>
      );
    case "textarea":
      return (
        <div className="input">
          {icon}
          <textarea
            type={type}
            required
            value={value}
            name={name}
            placeholder={placeholder}
            className={className}
            onChange={onChange}
            cols={cols}
            rows={rows}
            autoComplete={autoComplete}
          ></textarea>
        </div>
      );
    case "file":
      return (
        <div className="input file">
          <input
            type={type}
            {...options}
            name={name}
            accept={accept}
            onChange={onChange}
          />
          {children}
        </div>
      );

    case "submit":
      return (
        <div className="input">
          <input
            type={type}
            value={value}
            className={`input ${className}`}
            disabled={disabled}
            onClick={onClick}
          />
        </div>
      );
    default:
      return (
        <div className="input">
          {icon}
          <input
            type="text"
            required
            value={value}
            name={name}
            placeholder={placeholder}
            className={className}
            onChange={onChange}
            autoComplete={autoComplete}
          />
        </div>
      );
  }
};

export default Input;
